import React, {useState} from 'react'
import { Iconlogo, Menu, MenuItem, MenuItemLink, NavbarContainer, NavbarWrapper, IconLogoMobile } from './Navbar.elements'
import { FaBars} from "react-icons/fa";
import { GiCarnivorousPlant } from "react-icons/gi"
import { RiCloseCircleLine } from "react-icons/ri"

const Navbar = () => {
    const [click, setClick] = useState(false);

    const ChangeClick = () => {
        setClick(!click);
    };
    return (
        <>
        <NavbarContainer>
            <NavbarWrapper>
                <Iconlogo>< GiCarnivorousPlant size={"1.5em"}/>STORE</Iconlogo>
                <IconLogoMobile onClick={() => ChangeClick()}>
                    {click ? <RiCloseCircleLine/> : <FaBars/>}
                </IconLogoMobile> 
                <Menu click={click}>
                    <MenuItem  onClick={() => ChangeClick()}>
                        <MenuItemLink> HOME </MenuItemLink>
                    </MenuItem>
                    <MenuItem onClick={() => ChangeClick()}>
                        <MenuItemLink> PLANTS </MenuItemLink>
                    </MenuItem>
                    <MenuItem  onClick={() => ChangeClick()}>
                        <MenuItemLink> SHOP </MenuItemLink>
                    </MenuItem>
                    <MenuItem  onClick={() => ChangeClick()}>
                        <MenuItemLink> BLOG </MenuItemLink>
                    </MenuItem>
                    <MenuItem  onClick={() => ChangeClick()}>
                        <MenuItemLink> CONTACT </MenuItemLink>
                    </MenuItem>
                </Menu>
            </NavbarWrapper>
        </NavbarContainer>
        </>
    )
}

export default Navbar
