import React from "react";
import './App.css';
import Navbar from "./components/Navbar/Navbar";
import Colors from "./components/Colors";
import DetailsThumb from "./components/DetailsThumb";


class App extends React.Component{

  state = {
    products: [
      {
        "_id": "1",
        "title": "PLANTAS CARNIVORAS",
        "src": [
            "https://picsum.photos/id/227/500/500",
            "https://picsum.photos/id/247/500/500",
            "https://picsum.photos/id/257/500/500",
            "https://picsum.photos/id/267/500/500"
          ],
        "description": "Planta Carnivora",
        "content": "Planta carnivora bien chida",
        "price": 23,
        "colors":["GREEN","RED","BLUE","TEAL"],
        "count": 1
        }
    ],
    index: 0
  };

  myRef = React.createRef();

  handleTab = index =>{
    this.setState({index: index})
    const images = this.myRef.current.children;
    for(let i=0; i<images.length; i++){
      images[1].className = images[i].className.replace("active", "");
    }
    images[index].className = "active";
  };

  componentDidMount(){
    const{index} = this.state;
    this.myRef.current.children[index].className = "active";
  }

  render(){
    const {products, index} = this.state;
    console.log(products);
    return(
      <>
      <Navbar/>
      <div className="app">
        {
          products.map(item =>(
            <div className="details" key={item._id}>
              <div className="big-img">
                <img src={item.src[index]} alt=""/>
              </div>

              <div className="box">
                <div className="row">
                  <h2 className="tittleCard">{item.title}</h2>
                  <span>${item.price}</span>
                </div>

                <Colors colors= {item.colors} />

                <p className="itemDescription">{item.description}</p>
                <p className="itemContent">{item.content}</p>

                <DetailsThumb images={item.src} tab={this.handleTab} myRef={this.myRef} />
                  <button className="cart">Add to cart</button>
                </div>
            </div>
          ))
        }
      </div>
      </>
    )
  }
}


export default App;
